
import React, { useState } from "react"
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./App.css"

export default function Login() {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [auth, setAuth] = useState('');
    const [fullName,  setfullName] = useState('');
    const [selectValue, setSelectValue] = useState('');
    const [error, setError] = useState('');
    const [emailerror, setEmailError] = useState('');


    const data = [1, 2, 3]
    const obj = [{
      name : 'Admin'
    },
  {
    name : 'Student'
  },
{
  name : 'Teacher'
}]

    const nameHandler = (e) => {
        let em = setfullName(e.target.value);
        console.log(em);
     }

    const emailHandler = (e) => {
       let em = setEmail(e.target.value);
       console.log(em);
    }

    const passwordHandler = (e) => {
       let em = setPassword(e.target.value);
       console.log(em);
    }

    const selectHandler = (e) =>{
        e.preventDefault();
        setSelectValue(e.target.value);
    }

    const SignUpHandler =(e) => {
        e.preventDefault();
        setAuth(auth === "sign in" ? "sign up" : "sign in");
    }
    const navigate = useNavigate();

    const res = {
      email : email,
      password : password,
      selectValue : selectValue
  }

    const submitHandler =(e) =>{
        e.preventDefault();
      
 
        //Save user Details post req

        if(email === '') {
             console.log("err");
             setEmailError('Email required');
        }
        else
        setEmailError(null);
        if(password === '' || password.length < 8) {
          setError('password required')
        }
        else 
        {
        
          axios.post("http://localhost:5000/register", res)
          .then(function(res) {
            console.log(res);
            navigate('/Navbar');
         } 
          )
          .catch(function(err) {
              <div>Login Authentication failed</div>
              
          }); 
        
        }

    }


    if(auth === "sign in") {
        return (
            <div style={{background : 'black'}}>
            <div className="Auth-form-container">
              <form className="Auth-form">
                <div className="Auth-form-content">
                  <h3 className="Auth-form-title">Sign In</h3>
                  <div className="text-center">
                    Not Registered?
                    <span className="link-primary" onClick={SignUpHandler}>Sign Up</span>
                  </div>
                  <div className="form-group mt-3">
                    <label>Email address</label>
                    <input
                      type="email"
                      value={email}
                      onChange={emailHandler}
                      className="form-control mt-1"
                      placeholder="Enter email"
                    />
                  </div>
                  {
                    emailerror && <h5 style={{color : 'red'}}>{emailerror}</h5>
                  }
                 
                  <div className="form-group mt-3">
                    <label>Password</label>
                    <input
                      type="password"
                      value={password}
                      onChange={passwordHandler}
                      className="form-control mt-1"
                      placeholder="Enter password"
                    />
                  </div>
                  {
                    error && <h5 style={{color : 'red'}}>{error}</h5>
                  }
                  <div className="form-group mt-3">
                    <label>Select</label> <br/>
                    <select className="form-control mt-1"  onChange = {selectHandler}>
                    <option option={obj} >Choose </option>
                    {obj.map((ob, id) => {
                        return <option value = {ob.selectValue} 
                        key={id}>
                            {ob.name}
                        </option>
                    })}
                    
                </select>
                  </div>
                  <div className="d-grid gap-2 mt-3">
                    <button type="submit" className="btn btn-primary" onClick={submitHandler}>
                      Submit
                    </button>
                  </div>
                  <p className="forgot-password text-right mt-2">
                    Forgot <a href="#">password?</a>
                  </p>
                </div>
              </form>
            </div>
            </div>
          )
          
    }
    else {
           return (
            <div style={{background : 'blue'}}>
            <div className="Auth-form-container">
                <form className="Auth-form">
                    <div className="Auth-form-content">
                        <h3 className="Auth-form-title">Register</h3>
                            <div className="text-center">
                                Already Registered ? 
                                <span className="link-primary" onClick={SignUpHandler}>sign in</span>
                            </div>
                            <div className="form-group mt-3">
                                <label>Full Name</label>
                                <input
                                type="name"
                                value={fullName}
                                onChange={nameHandler}
                                className="form-control mt-1"
                                placeholder="name">

                                </input>
                            </div>
                            <div className="form-group mt-3">
                                <label>Email</label>
                                <input
                                type="email"
                                value={email}
                                onChange={emailHandler}
                                className="form-control mt-1"
                                placeholder="email">

                                </input>
                            </div>
                            <div className="form-group mt-3">
                                <label>Password</label>
                                <input
                                type="password"
                                value={password}
                                onChange={passwordHandler}
                                className="form-control mt-1"
                                placeholder="password">

                                </input>
                            </div>
                            <div className="d-grid gap-2 mt-3">
                    <button type="submit" className="btn btn-primary" onClick={submitHandler}>
                      Submit
                    </button>
                  </div>
                </div>
                </form>
            </div>
            </div>
           ) 
    }
 
}